import { AjaxHttp } from '../js/ajaxHttp'
import { getYMDFormateTime, isArrayType, getImageUrl } from "../js/utility.js"
import { mapState } from 'vuex'
import md5 from 'js-md5'

export default {
  name: 'welcome',
  data() {
    return {}
  },
  computed: {
    ...mapState([
      'welcomeInfo',
      'userInfo'
    ]),
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'login') {
      this.$store.commit('updateWelcomeInfo', {
        type: 'welcome', //平台类型与type相同 进行赋值
        redirect: this.$route.query.redirect,
        login: false,
      });
    } else {
      this.$store.commit('updateWelcomeInfo', {});
    }
    next();
  },
  mounted() {
    if (this.userInfo !== '' && typeof this.userInfo.token !== 'undefined' && typeof window.localStorage.wkurl !== 'undefined') {
      this.$router.replace({
        path: '/'
      });
    }

    if (this.welcomeInfo.type === 'welcome' && this.welcomeInfo.login) {
      if (typeof this.welcomeInfo.redirect !== 'undefined' && this.welcomeInfo.redirect !== '' && this.welcomeInfo.redirect !== '/login') {
        this.$router.replace({
          path: this.welcomeInfo.redirect
        });
      } else {
        this.$router.replace({
          path: '/'
        });
      }
    } else {
      if (this.$route.params.action === 'login' && typeof this.welcomeInfo.login === 'undefined') {
        this.$router.push({
          path: '/login'
        })
      }
    }
  },
  methods: {
    enterLogin() {
      this.$router.push({
        path: '/login'
      })
    },
    enterExperience() {
      window.localStorage.wkurl = 'http://demo2.crm.net/';
      AjaxHttp('POST', 'm=user&a=login', {
        name: 'admin',
        password: md5('123456')
      }, this, function(res, wself, success) {
        wself.canuse = true;
        if (success) {
          res.img = getImageUrl(res.img); //处理一下图片
          wself.getPermission(res);
          let user = res;

          //设置网页顶部标题
          document.title = res.system_name;

          wself.$store.dispatch('setUserInfo', user);
          window.sessionStorage.user = JSON.stringify(user);
          window.localStorage.user = JSON.stringify(user);
          window.localStorage.system_name = res.system_name;

          wself.welcomeInfo.login = true;
          wself.$store.commit('updateWelcomeInfo', wself.welcomeInfo);
          wself.$router.replace({
            path: '/'
          });
        }
      });
    },
    getPermission(res) {
      var temps = ['customer', 'business', 'knowledge', 'contacts', 'product', 'leads', 'contract', 'announcement', 'examine', 'event', 'sign', 'task', 'address_book'];
      if (res.admin === 1) {
        for (var i = 0; i < temps.length; i++) {
          res[temps[i]] = true;
        }
      } else {
        for (var j = 0; j < res.permission.length; j++) {
          res[res.permission[j]] = true;
        }
      }

    },
  }

}




